<template>
  <div class="phoneContainer">
    <div class="withBack">
      <img
        role="button"
        tabindex="0"
        alt=""
        @click="goBack"
        @keyup="goBack"
        class="backImg"
        src="../assets/뒤로가기.png"
      />
      <h2 class="word1">내용 추출 완료!</h2>
    </div>
    <div class="infDiv">
      <p
        v-if="fileContent"
        class="pre-wrap"
        :style="{ fontSize: fontSize + 'px' }"
      >
        {{ fileContent }}
      </p>
    </div>
    <div class="buttonGroup1">
      <button class="shortBtn1" @click="upFontSize">+ 확대</button>
      <button class="shortBtn1" @click="downFontSize">- 축소</button>
    </div>
    <router-link to="/cv">
      <button class="longBtn1">읽어주기</button>
    </router-link>
    <router-link to="/main">
      <button class="longBtn1">메인으로</button>
    </router-link>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "CameraSummary",
  components: {},
  data() {
    return {
      fileContent: null,
      currentTime: null,
      fontSize: 16,
    };
  },
  async mounted() {
    this.updateTime();
    this.timer = setInterval(this.updateTime, 60000);
    try {
      const response = await axios.get("/purba1/out1.txt");
      this.fileContent = response.data;
    } catch (error) {
      console.error("텍스트 파일을 불러오는 중 에러 발생:", error);
    }
  },
  methods: {
    goBack() {
      this.$router.back();
    },
    updateTime() {
      const now = new Date();
      const hours = now.getHours().toString().padStart(2, "0");
      const minutes = now.getMinutes().toString().padStart(2, "0");
      this.currentTime = `${hours}:${minutes}`;
    },
    upFontSize() {
      this.fontSize += 1; // 폰트 크기를 1px 증가
    },
    downFontSize() {
      if (this.fontSize > 1) {
        this.fontSize -= 1; // 폰트 크기를 1px 감소, 최소 1px로 설정
      }
    },
  },
};
</script>

<style>
@import "../css/index.css";
</style>
