<template>
  <div class="phoneContainer">
    <div class="withBack">
      <img
        role="button"
        tabindex="0"
        alt=""
        @click="goBack"
        @keyup="goBack"
        class="backImg"
        src="../assets/뒤로가기.png"
      />
      <h2 class="word1">2024년 11월 1일</h2>
    </div>
    <div class="imgDisplayed">
      <img class="imgblk2" src="../assets/img1.jpeg" />
    </div>
    <div class="festival-info">
      <p class="festival-title"></p>
      <p class="festival-details">
        일시 : <br />
        장소 :
      </p>
    </div>
  </div>
</template>

<script>
export default {
  name: "GallerySummary",
  components: {},
  data() {
    return { currentTime: null };
  },
  mounted() {
    this.updateTime();
    this.timer = setInterval(this.updateTime, 60000);
  },
  methods: {
    goBack() {
      this.$router.back();
    },
    updateTime() {
      const now = new Date();
      const hours = now.getHours().toString().padStart(2, "0");
      const minutes = now.getMinutes().toString().padStart(2, "0");
      this.currentTime = `${hours}:${minutes}`;
    },
  },
};
</script>

<style>
@import "../css/index.css";
</style>
